<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                新闻资讯
                <img class="search" src="../../assets/technology/icon_search@2x.png" alt="" @click="goSearch">
            </p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        @change="onTabChange"
                ></md-tab-bar>
                <md-scroll-view :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in listLeft"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.coverPicture" alt="">
                        <div class="article">
                            <p v-html="i.title"></p>
                            <span><img src="@/assets/home/icon_time@2x.png" alt="">{{i.updateTime}}</span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/news'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 1,
      // tab项
      items: [{ name: 1, label: '平台动态' }, { name: 2, label: '行业资讯' }],
      listLeft: [],
      isFinished: false,
      Height: '900px',
      listRight: [],
      searchParams: {
        typeId: 1,
        pageSize: 10,
        pageNumber: 1
      }
    }
  },
  mounted () {
    this.getList()
    this.Height = window.innerHeight - 84 - 55 + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.listLeft = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.searchParams.typeId = item.name
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/news/detail',
        query: { id: i.id }
      })
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.listLeft = this.listLeft.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        debugger
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position fixed
        top 88px
        width 100%
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.md-tab-bar .md-tab-bar-list
        border-top 0!important
    .md-scroll-view
        padding 20px
    .scroll-view-item
        margin-bottom 30px
        text-align center
        font-size 28px
        font-family DINAlternate-Bold
        height 174px
</style>
