import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'
import store from '@/store/index'

export function getList (data) {
  const url = store.getters.status ? apiPath.news.getList : apiPath.news.getListWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
export function getListWithoutToken (data) {
  const url = store.getters.status ? apiPath.news.getList : apiPath.news.getListWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
